/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  width: 100%;
  height: 100%; }

.title {
  font-size: 18px;
  margin-bottom: 2px;
  color: #222;
  line-height: 1.5em; }

.title span {
  font-size: 18px;
  font-weight: bold;
  color: #279af3;
  margin-right: 10px; }

.options {
  float: right;
  color: #29244e; }

.options a {
  float: right;
  cursor: pointer;
  margin: 0px 10px; }

.formContainer {
  margin-top: 35px;
  height: 100%; }

.formContainer textArea {
  width: 100%;
  height: 130px;
  border: 1px solid #e0e0e0;
  resize: none;
  padding: 10px; }

.formContainer button {
  position: relative;
  width: 100%;
  display: inline-block;
  padding: 9px 14px;
  text-align: center;
  margin-top: 10px;
  font-size: 14px;
  color: #fff;
  background-color: #11C26F;
  border: none;
  cursor: pointer;
  border-radius: 20px; }



