/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  width: 100%;
  height: 100%; }

.loading {
  width: 100%;
  height: 100%; }

.loading > div {
  position: relative;
  z-index: 10;
  width: 200px;
  height: 200px;
  top: 50%;
  left: 50%;
  margin-top: -110px;
  margin-left: -50px; }

.header {
  position: relative;
  width: 100%;
  padding: 10px 18px 2px 14px;
  font-size: 14px;
  text-align: right;
  color: #555;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.menu {
  display: inline;
  background-color: #fbfbfb;
  border: 1px solid #e0e0e0;
  padding: 7px 10px;
  margin: 10px 7px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.menu span {
  color: #333; }

.menu.options {
  background: #fff;
  cursor: pointer; }

.weeks,
.filters {
  display: inline; }

.weeks {
  margin-right: 18px; }

.weeks select {
  background: #fafafa; }

.weeks span {
  margin-right: 5px; }

.filters input[type=checkbox] {
  margin-left: 10px;
  margin-right: 4px; }

.filters label {
  font-size: 0.95em;
  color: #999; }

.panel {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 10px;
  margin-bottom: 20px; }



