/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  width: 100%;
  height: 100%;
  padding: 14px 14px 39px 14px; }

.text {
  font-size: 15px;
  margin-bottom: 5px; }

.dateContainer {
  float: right;
  font-size: 14px;
  padding: 4px 0px; }

.dateContainer .image,
.dateContainer .date {
  float: left; }

.dateContainer .image {
  width: 22px;
  height: 22px;
  margin-right: 10px;
  background-position: center center;
  background-repeat: no-repeat;
  border-radius: 50%; }

.dateContainer .date {
  font-style: italic;
  color: #777; }

.options {
  position: absolute;
  background: #fff;
  right: 30px;
  display: none; }

.options .option {
  display: inline-block;
  margin: 2px;
  font-size: 13px;
  color: white;
  padding: 4px 5px;
  cursor: pointer;
  border-radius: 50%; }

.delete {
  background-color: #FF8050; }



