/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  display: block;
  width: 100%;
  height: 100%;
  padding: 12px;
  cursor: pointer; }

.title {
  font-size: 14px;
  padding-bottom: 4px;
  color: #777; }

.title i {
  color: #a0a0a0; }

.title a {
  color: #777;
  font-family: inherit; }

.title .numH {
  color: #F0B010;
  font-size: 0.85em;
  margin-left: 4px; }

.options {
  position: absolute;
  top: 10px;
  background: #fff;
  right: 10px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  opacity: 0;
  /* Hide icons initially */
  display: -ms-flexbox;
  display: flex;
  /* Flex display for single-line alignment */
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
  /* Smooth transition when shown */ }

.module:hover .options {
  opacity: 1;
  /* Show icons */
  position: absolute;
  /* Place them correctly when visible */ }

.options .option {
  display: inline-block;
  margin: 2px;
  font-size: 13px;
  color: white;
  padding: 4px 5px;
  border-radius: 50%; }

.add {
  background-color: #198CE5; }

.info {
  background-color: #FFB500; }

.edit {
  background-color: #f0a000; }

.bot {
  background-color: #FF5F00; }

.insight {
  background-color: #20b068; }

.add:hover {
  background-color: #1077D5; }

.info:hover {
  background-color: #FFA000; }

.edit:hover {
  background-color: #f08500; }

.bot:hover {
  background-color: #f08500; }

.insight:hover {
  background-color: #10a060; }

.container {
  position: relative;
  width: 100%; }

.container.other {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.wrapperOther {
  position: relative;
  width: 25%;
  padding: 0px 10px 0px 10px; }

.item {
  padding: 8px 0px 6px 0px; }



